header{
    position: fixed;
    top:0;
    left: 0px;
    z-index: 999;
    height: 45px;
    width: 100%;
    display:flex;
    align-items:center;
    justify-content:flex-start;
    background-color: transparent;
}
header a {
    text-decoration: none;
    color: inherit;
    padding: 3px;
    margin: 0px;
    font-weight: bold;
    border-radius: 1px;
    text-align: center;
    color: #111;
    cursor: pointer;
}
header img{
    width: 70px;
    margin: auto 6px;
    box-sizing: border-box;
}
.escolher{
   
    z-index: 999;
    height: 40px;
    display:flex;
    align-items:center;
    justify-content:center;
}
.escolher .button{
    margin: auto 2px;
    padding: 5px;
    background-color: rgb(173, 10, 10);
    color: #fff;
    font-weight: bold;
    border: none;
    cursor: pointer;
}

.escolher .button:active{
    background-color: rgb(10, 173, 108);
}

@media screen and (max-device-width: 650px) and (orientation: portrait) {
    header{
        justify-content:center;
    }
}