@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
*{
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}
body{
  background-color: #111;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  padding: 0px;
  margin: 0px;
}
/* width */
::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

input[type=number]::-webkit-inner-spin-button { 
  -webkit-appearance: none;
  
}
input[type=number] {
 -webkit-appearance: textfield;
         appearance: textfield;
}

.movieRow{
    margin-bottom: 0px;
}
.movieRow h2 {
    margin-left:  30px;
    font-size:x-large;
}
.movieRow--listarea{
    overflow: hidden;
}
.movieRow--list{
    transition: 0.7s;
    padding-left: 30px;
    display: inline-flex;
    flex-wrap: nowrap;
}
.movieRow--item{
    display: inline-flex;
    cursor: pointer;   
}
.movieRow--item img {
    -webkit-transform: scale(.9);
            transform: scale(.9);
    transition: all ease .2s;
}
.movieRow--item img:hover {
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: all ease .2s;
}
.movieRow--left,
.movieRow--right{
    position:absolute;
    width:40px;
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 5%;
    z-index: 99;
    display: flex;
    align-items: center;    
    margin: 0px;
    padding: 0px 5px;
    overflow:hidden;
    opacity: 0;
    transition: 0.5s;
    -webkit-transform: scale(.9);
            transform: scale(.9);
}
.movieRow--left{
    left:0px;
    justify-content: flex-start;
}
.movieRow--right{
    right: 0px;
    justify-content: center;
}

.movieRow:hover .movieRow--left,
.movieRow:hover .movieRow--right{
    opacity: 1;}
    
    .movieRow--left:hover,
    .movieRow--right:hover{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    
    @media (max-width:760px) {
        .movieRow--left,
        .movieRow--right{
            opacity: 1;
        }
    }
iframe{
    width: 100%;
    height: 99.3vh;
    object-fit: fill;
    overflow: hidden;
    padding: 0px;
    margin: 0px;
    z-index: 998;

}
.Comands{
    position: fixed;
    top:0;
    right: 0px;
    z-index: 999;
    height: 40px;
    display:flex;
    align-items:center;
    justify-content:center;
}
.Comands button{
    margin: auto 2px;
    padding: 5px;
    background-color: rgb(173, 10, 10);
    color: #fff;
    font-weight: bold;
    border: none;
    cursor: pointer;
}
.Comands select{
    margin: auto 2px;
    padding: 4px;
    background-color: rgb(173, 10, 10);
    color: #fff;
    font-weight: bold;
    border: none;
    cursor: pointer;
    text-align: center;
    outline: none;
    border: none;
}

select option{
    border: none;
    font-weight: bold;
    margin: 0px 5px;
}
.Comands h4{
    font-weight: bold;
}

@media screen and (max-device-width: 650px) and (orientation: portrait) {
    iframe {
        position: absolute;
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        -webkit-transform-origin: bottom left;
                transform-origin: bottom left;
        width: 100vh;
        height: 100vw;
        padding: 0px;
        margin: -100vw 0px 0px 0px;
        object-fit: cover;
        top: 0px;
        left: 0px;
    }
    .Comands{
        position: absolute;
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        bottom: 0px;
        right: 0px;
        padding: 0px;
        margin: 80% -16%;       
    }
}


.featured{
    height: 90vh;
    margin-bottom: -80px;
}
.featured--Vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;
}

.featured--name{
    font-size: 3vmax;
    font-weight: bold;
    margin-right: 15px;
}
.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
}

.featured--points,.featured--year, .featured--seasons{
    display: inline-block;
    margin-right: 15px;
}
.featured--points{
    color: #43d369;
}

.featured--description{
    margin-top: 15px;
    font-size: 16px;
    color: #999;
    max-width: 40%;
    max-height: 40%;
}
.featured--buttons{
    margin-top: 15px;
}
.featured--watch{
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    border-radius: 5px;
    padding: 15px 25px;
    text-decoration: none;
    margin: 10px 0px;
    background-color: #fff;
    color:#000;
    transition: .2s
}
.featured--watch:hover{
    opacity: .7;
    transition: .2s
}

.featured--genres{
    margin: 0px 15px;
    font-size: 18px;
    color: #999;
}

@media (max-width:760px) {
    .featured{
        height: 90vh;
    }
    .featured--name{
        font-size:5vmax
    }
    .featured--info{
        font-size: 16px;
    }
    .featured--description{
        font-size: 14px;
        color: rgb(233, 230, 230);
        max-width: 100%;
        max-height: 30%;
        padding-right: 30px;
    }
    .featured--watch{
        font-size: 16px;
        padding: 10px 15px;
    }
    .featured--genres{
        font-size: 14px;
    }
}
header{
    position: fixed;
    top:0;
    left: 0px;
    z-index: 999;
    height: 45px;
    width: 100%;
    display:flex;
    align-items:center;
    justify-content:flex-start;
    background-color: transparent;
}
header a {
    text-decoration: none;
    color: inherit;
    padding: 3px;
    margin: 0px;
    font-weight: bold;
    border-radius: 1px;
    text-align: center;
    color: #111;
    cursor: pointer;
}
header img{
    width: 70px;
    margin: auto 6px;
    box-sizing: border-box;
}
.escolher{
   
    z-index: 999;
    height: 40px;
    display:flex;
    align-items:center;
    justify-content:center;
}
.escolher .button{
    margin: auto 2px;
    padding: 5px;
    background-color: rgb(173, 10, 10);
    color: #fff;
    font-weight: bold;
    border: none;
    cursor: pointer;
}

.escolher .button:active{
    background-color: rgb(10, 173, 108);
}

@media screen and (max-device-width: 650px) and (orientation: portrait) {
    header{
        justify-content:center;
    }
}
